.header {
    display: flex;
    justify-content: space-between;
    margin-top: 39px;
    margin-bottom: 36px;
    & a {
        display: flex;
        align-items: center;
        font-size: 18px;
        line-height: 25px;
        color: $black;
    }
    &__account {
        display: flex;
        align-items: center;
        & svg {
            margin-right: 10px;
        }
    }
    &__search {
        background: url("../img/serch.svg");
        width: 21px;
        height: 21px;
        background-color: transparent;
        background-position: center center;
        border: none;
        margin-left: 35px;
        position: relative;
        &::before {
            content: "";
            border-left: 1px solid $peach;
            width: 0;
            height: 20px;
            position: absolute;
            top: 0;
            left: -15px;
        }
    }
}