.arrow {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 47px;
    height: 47px;
    border: 2px solid transparent;
    background-color: $orange;
    &:not(:last-child) {
        margin-right: 20px;
    }
    &-right {
        background: transparent;
        border-color: $milk;
    }
}