.slider {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 26%;
    &__arrow {
        display: flex;
    }
    &__line {
        width: 53%;
        height: 2px;
        background: $milk;
    }
    &__counter {
        font-weight: 500;
        font-size: 24px;
        line-height: 33px;
        color: $orange;
    }
}