.top-destinations {
    position: relative;
    display: flex;
    margin-top: 16%;
    &::after {
        content: "";
        background: $grey-transparent;
        width: 789px;
        height: 580px;
        position: absolute;
        top: -25%;
        right: -5.57%;
        z-index: -1;
    }
    &__content {
        width: 25%;
        margin-right: 5%;
        & h2 {
            margin: 0;
            font-weight: 800;
            font-size: 48px;
            line-height: 65px;
            color: $black;
            padding-bottom: 20px;
        }
        & span {
            color: $orange;
        }
        & p {
            padding-top: 20px;
            padding-bottom: 60px;
            font-family: "Lexend Deca", "Tahoma", sans-serif;
            font-weight: 400;
            font-size: 15px;
            line-height: 26px;
            color: $wheat;
        }
    }
    &__country{
        display: flex;
        width: 75%;
    }
    &__img {
        position: relative;
        &:not(:last-child) {
            margin-right: 30px;
        }
        &::after {
            content: "";
            background: linear-gradient(180deg, rgba(255, 255, 255, 0) 47.69%, rgba(0, 0, 0, 0.8) 100%);
            width: 100%;
            height: 99%;
            position: absolute;
            top: 0;
            left: 0;
        }
        & span {
            position: absolute;
            bottom: 30px;
            left: 30px;
            font-weight: bold;
            font-size: 28px;
            line-height: 38px;
            color: $white;
            z-index: 1;
        }
    }
}