.travel-now {
    &__list {
        position: relative;
        display: flex;
        align-items: center;
        background: url("../img/hero-banner.jpg");
        width: 100%;
        height: 1000px;
        margin-bottom: 20px;
    }
    &__name {
        padding-left: 8%;
        &-header{
            font-weight: bold;
            font-size: 84px;
            line-height: 112px;
            max-width: 755px;
            margin: 0;
            align-items: center;
            color: $black;
            padding-bottom: 50px;
        }
    }
}

@media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    .travel-now {
        &__list {
            background-image: url('../img/hero-banner@2x.jpg');
        }
    }
}