.explore {
    padding-top: 5%;
    display: flex;
    justify-content: center;
    &__list {
        padding: 0;
        margin: 0;
        width: 84.4%;
        display: flex;
        justify-content: center;
    }
    &__item{
        cursor: pointer;
        padding: 30px 40px;
        background: $grey;
        display: flex;
        flex-direction: column;
        align-items: center;
        &:not(:last-child) {
            margin-right: 30px;
        }
        &:hover {
            background: $white;
            filter: drop-shadow(0px 12px 30px rgba(200, 180, 180, 0.2));
        }
    }
    &__photo {
        height: 50%;
        & img {
            align-items: center;
            justify-content: center;
        }
    }
    &__text {
        height: 50%;
        & h3 {
            text-align: center;
            font-weight: bold;
            font-size: 24px;
            line-height: 33px;
            color: $black;
            margin: 0;
        }
        & p {
            text-align: center;
            font-family: "Lexend Deca", "Tahoma", sans-serif;
            font-weight: 400;
            font-size: 15px;
            line-height: 26px;
            color: $wheat;
            margin: 20px 0 30px 0;
        }
    }
}